
.el-dialog__title {
  font-style: normal;
  font-size: 16px;
  color: #515151;
}
.froms {
  padding: 20px;
}
.froms .wwb-td {
    border: 1px solid #DFE6EC;
    padding: 10px 0;
    border-top: 0;
    text-align: center;
    margin-bottom: 20px;
}
.froms .wwb-td span {
      color: #484848;
}
.froms .wwb-td span i {
        color: #CD9890;
        font-style: normal;
        font-size: 14px;
}
.froms .wwb-di:after {
    clear: both;
    content: '';
    display: block;
}
.froms .wwb-di {
    height: 50px;
    width: 100%;
}
.froms .wwb-di .wwb-btn {
      width: 90px;
      float: right;
}
.el-table__row {
  text-align: center;
}
.el-table__row .el-table_1_column_1 {
    padding: 9px 0;
}



